import fs from 'fs';
import { PATH_MULTI_STEP_UPLOAD } from '../constants';
import { multiGlob } from './file';
/**
 * Orchestrates the multi-step upload process.
 * @param args
 * @param context
 */
export async function multiStepUpload(args, context) {
    const { logger } = context;
    const { label, report, root, sha } = args;
    logger.info(`Label: ${label}`);
    logger.info(`Root: ${root}`);
    logger.info(`SHA: ${sha}`);
    const filenames = multiGlob(report, context);
    logger.group('Requesting signed urls');
    const { keys, urls, signature } = await getSignedUploadUrls(args, filenames, context);
    logger.groupEnd();
    logger.group('Uploading reports');
    await uploadToSignedUrls(filenames, urls, context);
    logger.groupEnd();
    logger.group('Finalizing upload');
    await finishMultistepUpload(args, keys, signature, context);
    logger.groupEnd();
}
/** Fetches signed URLs */
export async function getSignedUploadUrls(args, filenames, { client }) {
    const { label, nodeCount, nodeIndex, root, sha, token } = args;
    const response = await client.post(PATH_MULTI_STEP_UPLOAD, { filenames, label, nodeCount, nodeIndex, root, sha }, {
        auth: { password: token, username: 'token' },
        maxContentLength: Infinity,
        timeout: 30000,
    });
    return response.data;
}
/** Uploads directly to S3. */
export async function uploadToSignedUrls(filenames, urls, { client }) {
    for (const filename of filenames) {
        const stream = fs.createReadStream(filename);
        await client.put(urls[filename], stream, {
            headers: {
                'Content-Length': String((await fs.promises.stat(filename)).size),
            },
        });
    }
}
/**
 * Informs Check Run Reporter that all files have been uploaded and that
 * processing may begin.
 */
export async function finishMultistepUpload(args, keys, signature, { client }) {
    const { label, nodeCount, nodeIndex, root, sha, token } = args;
    const response = await client.patch(PATH_MULTI_STEP_UPLOAD, {
        keys,
        label,
        nodeCount,
        nodeIndex,
        root,
        sha,
        signature,
    }, {
        auth: { password: token, username: 'token' },
        maxContentLength: Infinity,
    });
    return response;
}
